




export default {
	name: 'cmd-icon',

	props: {
		/**
		 * 自定义字体图标库前缀
		 */
		prefixClass: {
			type: String,
			default: 'cmd-icon'
		},
		/**
		 * 图标类型
		 */
		type: String,
		/**
		 * 图标颜色
		 */
		color: {
			type: String,
			default: '#fff'
		},
		/**
		 * 图标大小
		 */
		size: {
			type: [Number, String],
			default: '24'
		}
	},

	computed: {
		setStyle() {
			return `color:${this.color};
			font-size:${this.size}px;
			line-height:${this.size/2}px`
		}
	},

	methods: {
		$_click(e) {
			this.$emit('click', e)
		}
	}
}
