














































































































































































































import cmdAvatar from "@/components/cmd-avatar/cmd-avatar.vue"
import productList from "@/components/product.vue"

export default {

	data() {
		return {
			titleNViewBackground: '',
			swiperCurrent: 0,
			swiperLength: 0,
			carouselList: [],
			//product
			product: {
				productCurrentPage: 1, //当前页
				productLastPage: 1, // 最后一页
				productPageSize: 10, //每页数量
				productList: [] //产品列表
			},
			//category
			category: []

		};
	},
	onLoad() {
		this.loadData();
	},
	computed: {
		categoryList: function() {
			let needReturn = [];
			for (let i in this.category) {
				needReturn[i] = {
					firstCharOfName: this.category[i].name.substr(0, 1),
					name: this.category[i].name,
					id: this.category[i].id
				};
			}
			return needReturn;
		}
	},
	methods: {
		/**
		 * 请求静态数据只是为了代码不那么乱
		 * 分次请求未作整合
		 */
		async loadData() {
			let carouselList = await this.$api.json('carouselList');
			this.titleNViewBackground = carouselList[0].background;
			this.swiperLength = carouselList.length;
			this.carouselList = carouselList;
			
			await this.getCategoryList();
			await this.getProductList({}, () => {
				this.$load.loadding('加载中……');
			}, () => {
				this.$load.endLoadding();
			}); //获取商品列表
			
		},
		//轮播图切换修改背景色
		swiperChange(e) {
			const index = e.detail.current;
			this.swiperCurrent = index;
			this.titleNViewBackground = this.carouselList[index].background;
		},
		//详情页
		navToDetailPage() {
			uni.navigateTo({
				url: '/pages/detail/detail'
			})
		},
		//产品详情
		navToProductDetail() {
			uni.navigateTo({
				url: '/pages/product/product'
			})
		},
		//产品列表
		navToProductList(option = {}) {
			let queryStr = "";
			if (option) {
				for (let i in option) {
					queryStr += (i + '=' + option[i]) + '&';
				}
			}
			if (queryStr.length) {
				uni.navigateTo({
					url: '/pages/product/list' + '?' + queryStr.replace(/(&)$/g, '')
				})
			} else {
				uni.navigateTo({
					url: '/pages/product/list'
				})
			}
		},
		// 搜索框确认搜索
		confirmSearch(event) {
			this.navToProductList({
				keyWords: event.detail.value
			})
		},
		// 查看分类下的商品
		navToCategory(id) {
			this.navToProductList({
				category_id: id
			})
		},
		//ajax 方法

		//获取商品列表
		getProductList: function(option = {}, startCallBack = () => {}, endCallback = () => {}) { //获取商品列表
			startCallBack()
			let data = {
				page: 1,
				pageSize: 10
			};
			for (let i in option) {
				data[i] = option[i];
			}

			let token = this.$ajax.getToken();
			uni.request({
				method: 'GET',
				url: this.$ajax.ajaxBaseUrl + '/api/commodities',
				dataType: 'json',
				data: data,
				header: {
					'content-type': 'application/json',
					"Authorization": token.token_type + ' ' + token.access_token
				},
				success: (res) => {
					if (res.data.status === 0) {
						this.product.productCurrentPage = res.data.data.current_page; //当前页
						this.product.productLastPage = res.data.data.last_page; //最后一页
						this.product.productPageSize = res.data.data.per_page; //每页数量
						if (data.page > 1 && this.product.productList.length) {
							this.product.productList = this.product.productList.concat(res.data.data.data);
						} else {
							this.product.productList = res.data.data.data;
						}
					} else {
						this.$api.msg(res.data.message);
					}
					endCallback();
				}
			});
		},

		//获取商品分类
		getCategoryList: function() { //获取商品分类
			this.$load.loadding('加载中……');
			let token = this.$ajax.getToken();
			uni.request({
				method: 'GET',
				url: this.$ajax.ajaxBaseUrl + '/api/commodities_category',
				dataType: 'json',
				header: {
					'content-type': 'application/json',
					"Authorization": token.token_type + ' ' + token.access_token
				},
				success: (res) => {
					if (res.data.status === 0) {
						this.category = res.data.data;
					} else {
						this.$api.msg(res.data.message);
					}
					this.$load.endLoadding();
				}
			});
		},
	},
	components: {
		cmdAvatar,
		productList
	},

























	onPullDownRefresh: async function() {
		this.getProductList({}, () => {}, () => {
			uni.stopPullDownRefresh();
		}); //获取商品列表
	},
	onReachBottom: async function() {
		if (this.product.productCurrentPage < this.product.productLastPage) {
			this.getProductList({
				page: this.product.productCurrentPage + 1
			}, () => {
				this.$load.loadding('加载更多中……');
			}, () => {
				this.$load.endLoadding();
			}); //获取商品列表
		}
	}
}
